import { Col, Container, Tab, Row } from "react-bootstrap";
import { Navbar, Nav } from "react-bootstrap";
import { ProjectInfo } from "./ProjectInfo";
import projImg1 from "../assets/img/stock.webp";
import projImg2 from "../assets/img/movie.webp";
import projImg3 from "../assets/img/crime.webp";

export const Projects = () => {

  const projects = [
    {
      title: "Stock Purchase Simulator",
      description: "Final Project done as part of Harvard's CS50 course. Helps users to purchase stocks similar to investopedia.",
      imgUrl: projImg1,
      link: "https://github.com/WinThant16/CS50-2020-2021/tree/main/WinThant16-cs50-problems-2021-x-project/WinThant16-cs50-problems-2021-x-project",
    },
    {
      title: "Movie Recommender",
      description: "Developed a movie recommender in C++ in a team of 4, as part of the CS100 course at UCR.",
      imgUrl: projImg2,
      link: "https://github.com/WinThant16/MovieRecommender",
    },
    {
      title: "Crime Analysis using Pyspark (In Progress)",
      description: "Analyze crime patterns in five major cities across the United States using Pyspark, as part of the CS179G Project course at UCR",
      imgUrl: projImg3,
      link: "https://docs.google.com/document/d/1FhQOC_8CuGTowFZgOtC6kUCM_S_9DRvy62JGSTed2vE/edit?usp=sharing",
    },
    
  ];
  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col>
            <h2><span className="tagline">Projects and Activities</span></h2>
            <p>Here are some projects I've worked on:</p>
            <Tab.Container id="projects-tabs" defaultActiveKey="first">
              {/* <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                <Nav.Item>
                  <Nav.Link eventKey="first">Tab 1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="second">Tab 2</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="third">Tab 3</Nav.Link>
                </Nav.Item>
              </Nav> */}
              <Tab.Content>
                <Tab.Pane eventKey="first">
                  <Row>
                    {
                      projects.map((project, index) => {
                        return (
                          <ProjectInfo key={index} {...project} />
                        )
                      })
                    }
                  </Row>
                </Tab.Pane>
                
                
              </Tab.Content>
            </Tab.Container>
          </Col>
        </Row>
      </Container>
    </section>
  )
}